<template>
    <div>
               <div ref="swpier-wrap" class='swpier-wrap swiper-container'>
                   <div class="swiper-wrapper">
                       <div class="swiper-slide" v-for="(item,index) in details.GoodsPics" :key="index">
                           <el-image class="swiper-image" :src="item"></el-image>
        <!--                   <img :src="item" alt="" />-->
                       </div>
                   </div>
                   <div ref="swiper-pagination" class="swiper-pagination"></div>
               </div>
               <div class='goods-ele-main'>
                   <div class='goods-title'>{{details.Title}}</div>
                   <div  class='sales-wrap'>
                       <div>单价:<label class='price'>¥{{details.Price}}</label></div>
                       <div>积分:<label class='point'>{{details.Point}}</label></div>
                   </div>
                   <div class='sales-wrap'>
                       <div>已兑换：{{details.QuantitySold}}件</div>
                       <div>总数量：{{details.GoodsCount}}件</div>
                   </div>
               </div>
               <div class='reviews-main'>
                   <div class="reviews-title">商品详情</div>
               </div>

               <div class='bottom-btn-wrap'>
                   <div>
                       数量：
                       <el-input-number class="loose-input"  v-model="aCount" :precision="0" :step="1" :min="0"></el-input-number>
                   </div>
                   <el-button class="bottom-btn" @click="buy">购买</el-button>
               </div>
               <div  v-html="details.Content"></div>
    </div>
</template>

<script>
    import { request } from '../../utils/http';

    export default {
        data: function() {
            return {
                query:{
                    Id:0,
                    UserId:0,
                    Sid:'',
                },
                aCount:1,
                details:{
                    Title:'',
                    Price:0,
                    Point:0,
                    QuantitySold:0,
                    GoodsCount:0,
                    Content:'',

                    GoodsPics:[require('../../assets/img/a33.jpg'),require('../../assets/img/default_avatar.jpg')]
                },
            };
        },
        methods: {
            getData() {
                console.log(this.query);
                request({url: 'api/getgoodsdetailex', method: 'post',data: this.query }).then((res)=>{
                    if (res.code==200){
                        this.details = res.obj
                    } else {
                        this.$router.push('/Root404')
                    }
                });
            },
            buy() {
                let p ={
                    GoodsCount:this.aCount,
                    UserId:this.query.UserId,
                    Sid:this.query.Sid,
                    GoodsId:this.query.Id
                }
                request({url: 'api/buygoods',method: 'post', data:p }).then((res)=>{
                    if (res.code==200){
                        this.details.QuantitySold=this.details.QuantitySold+this.aCount;
                        this.$message.success('购买成功,花费积分：'+res.obj);
                    } else {
                        this.$message.warning('购买失败:'+res.msg);
                    }
                });
            },
            load () {

            }
        },
        created(){
            this.query.Id = this.$route.query.id;
            this.query.UserId = this.$route.query.UserId;
            this.query.Sid = this.$route.query.Sid;

            this.getData();
        }
    };
</script>

<style scoped>
    .swpier-wrap{width:100%;height:17rem;overflow:hidden;position: relative;z-index:1}
    .swpier-wrap img{width:100%;}
    .goods-ele-main{width:100%;height:auto;background-color:#FFFFFF;margin:0 auto;padding-top:0.5rem;padding-bottom: 0.5rem;}
    .goods-ele-main .goods-title{width:90%;font-size:1.28rem;margin:0 auto;}
    .goods-ele-main .sales-wrap{width:90%;margin:0 auto;font-size:1.24rem;color:#969696;display:flex;display:-webkit-flex;justify-content: space-between;-webkit-justify-content: space-between;margin-top:0.2rem;}
    .price{color:#F93036}
    .point{color:#F93036}

    .reviews-main{width:100%;background-color: #FFFFFF;margin-top:0.5rem;overflow:hidden;padding-bottom: 1.7rem}
    .reviews-main .reviews-title{color:#7b7f82;font-size:1.32rem;width:90%;margin:0 auto;margin-top:0.2rem;}
    .reviews-main .reviews-wrap{width:90%;margin:0 auto;}

    .swiper-slide{
        width: 100%;
    }

    .swiper-image{
        margin:0 auto;
    }
    .loose-input {
        font-size:1.32rem;
    }
    .bottom-btn-wrap{width:100%;
        font-size:1.32rem;
        padding:10px;
        position:fixed;
        bottom:0px;left:0px;z-index: 10;
        display:flex;display:-webkit-flex;
        background-color: #F93036;
        justify-content: space-between;-webkit-justify-content: space-between;
    }
    .bottom-btn{
        margin-right: 20px;
        font-size:1.32rem;
    }
    .content{width:90%;margin:0 auto;font-size:0.32rem;line-height:0.5rem;}
</style>